export default {
	components: {
		uniSwipeAction
	},
	data() {
		return {
			options: [{
				text: '删除',
				style: {
					backgroundColor: "rgba(255,84,31,1)"
				}
			}],
			goodsList: [{
					id: 1,
					img: '/static/img/goods/p1.jpg',
					name: '商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',
					spec: '规格:S码',
					price: 127.5,
					number: 1,
					selected: false
				},
				{
					id: 2,
					img: '/static/img/goods/p1.jpg',
					name: '商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',
					spec: '规格:S码',
					price: 127.5,
					number: 1,
					selected: false
				},
				{
					id: 3,
					img: '/static/img/goods/p1.jpg',
					name: '商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',
					spec: '规格:S码',
					price: 127.5,
					number: 1,
					selected: false
				},
			]
		}
	},
	methods: {
		bindClick(options){
			console.log(options)
			this.goodsList=this.goodsList.splice(options,2)
			console.log(this.goodsList)
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},

		touchEnd(index, $event) {
			//解除禁止触发状态
			this.isStop = false;
		},

		//删除商品
		deleteCoupon(id, List) {
			let len = List.length;
			for (let i = 0; i < len; i++) {
				if (id == List[i].id) {
					List.splice(i, 1);
					break;
				}
			}
			this.oldIndex = null;
			this.theIndex = null;
		},
	}
}
